
h1 {
    margin: 15px;
    text-align: center;
}

.buttons {
    display: flex;
    justify-content: space-between;
}

.btn-success {
    margin-left: auto;
}

.form-label span {
    font-size: 24px;
}

#clear {
    margin-right: 10px;
}



#decipher {
    margin-right: 10px;
}

#translate {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 150px;
}

#result {
    margin-top: 30px;
    font-size: 16px;
}

#spinner {
    display: none;
    margin-right: 5px;
}

#spinner.visible {
    display: block;
}


@media (max-width: 768px) {
    #clear {
        margin-bottom: 15px;
    }

    #decipher {
        margin-bottom: 15px;
    }

    .buttons-left {
        width: 100px;
    }

    .buttons-right {
        width: 135px;
    }
}